@import url(https://fonts.googleapis.com/css2?family=Signika&display=swap);
body {
  margin: 0;
  font-family: 'Signika', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}



.App {
  text-align: center;
  height: 100vh;
  width:100vw;
  display: flex;
  background-color: #FFCB05;
}




#gameboy{
    height: 100%;
    width:100%;
    display: flex;
    justify-content:center;
}
.gameboy{
    display:flex;
    width: 360px;
    height:592px;
    margin: auto;
    background-color: #cc0000;
    border-radius: 20px 20px 100px 20px;
    box-shadow: 5px 5px 5px;
    flex-direction: column;

}
.row{
    flex:1 1;
    justify-content: center;
}
.row2{
    display: flex;
    justify-content:center;
    flex-direction: row;
}
.col{
    flex:1 1;
    margin-top:1.5em;
    margin-left: 1em;
}
.col2{
    flex:2 1;
}
  .cross{
    height: 5em;
    margin: auto;
  }
  .greenbox{
      height:3em;
      width:2em;
  }

#PokeSearch{
    margin-left: auto;
    margin-right: auto;
}
.PokeSearch{
    z-index: 10;
    margin: auto;
    display:flex;
    flex-direction: column;
    width: 360px;
}
.searchBox{
    flex: 1 1;
    width:300px;
    margin: auto;
}
.rowButtons{
    flex:2 1;
    display: flex;
    flex-direction: row;
    height: 400px;
    width:350px;
    justify-content: space-around;
}
.section{
    flex:1 1;
    margin-left:auto;
    margin-right:auto;
    justify-content: space-around;
}
.plusButton{
    height: 75px;
    padding-top:3em;
    margin-left: 1em;
}
.bottomButtons{
    margin-top: 13rem;
    justify-content: space-between;
}

.aboutButton{
    display: inline;
    margin-right: 4px;
    background-color: black;
    border-color: darkslategrey;
}
.statsButton{
    display: inline;
    margin-left: 4px;
    background-color: black;
    border-color: darkslategrey;
    margin-bottom: 8px;
}
.labels{
    display: inline;
    padding: 5px;
    color:black;
    padding-top: 5px;
}

.changeButtons{
    height:250px;
    padding-top: 45px;
}
.upButton{
    background-color: black;
    border-radius: 50%;
    border: darkslategrey 1px solid;
    height: 45px;
    width: 45px; 
    color:white;
    display: flex;
    margin-left: 35px;
    padding:13px;
}
.downButton{
    background-color: black;
    border: darkslategrey 1px solid;
    border-radius: 50%;
    height: 45px;
    width: 45px; 
    color:white;
    display: flex;
    padding:7px;
}
.pokewindow{
    height:250px;
    width:275px;
    background-color: lightgray;
    border-radius: 10px;
    border: black 20px solid; 
    margin-left: auto;
    margin-right:auto;
    margin-top: 10px;
    display: flex;
}

.pokeball{
    height: 6em;
    width:12em;
    margin:auto;
}


.boxes{
    display: flex;
    flex-direction: column;
    margin: auto;
    margin-left:10px;
}
.infoBox{
    border: 2px solid black;
    border-radius: 5px;
    width:250px;
    height: 40px;
}
.pokeimage{
    height: 12em;
    width:12em;
    margin-left: auto;
    margin-right:auto;
    margin-top:0em;
    display:flex;
}
.name{
    font-size: 18px;
    margin:auto;
    margin-top: 6px;
}
h2 {
    text-transform: capitalize;
    display: inline; 
}

h3 {
    display: inline;
    padding-left: 10px;
}
.infoLabel{
    margin-top:-16px;
    margin-bottom:2px;
    font-size: 18px;
}
.boxesAb{
    display: flex;
    flex-direction: column;
    margin:auto;
}
.aboutTable{
    text-align: left;
    border-collapse: collapse;
    width:250px;
}

.aboutTable > tbody> tr >td{
    border: 2px solid black;
    font-size: 16px;
    padding:2px;
    text-transform: capitalize;
}

.statsTable{
    text-align: left;
    border-collapse: collapse;
    margin-top:-20px;
    width: 250px;
}
.statsTable >tbody > tr > td{
    border: 2px solid black;
    font-size: 16px;
    padding:2px;
}
